import React, { Component } from "react";
import "./additem.css"

class AddItem extends Component{

    state = {
        text :'',
        priority:'',
        state:true
    }
    handelChange = (e) =>{
        this.setState({
            //bdl mnktb text f line w priority f line kda ht4t8l m3 el etnen mra wa7da
            [e.target.id]:e.target.value
        })
    }
    handelSubmit =(e)=>{
       if (e.target.text.value!== '' && e.target.priority.value!== '' )
        {
        this.setState({
            text :'',
            priority:'',
        });
        e.preventDefault();
        this.props.addItem(this.state);
        }
        else
        e.preventDefault();
    }
    render(){
        return(
            <div className= "container content">
                <form onSubmit={this.handelSubmit}>
                 <input type="text" placeholder="Write Todo here" id="text" onChange={this.handelChange} value={this.state.text} />
                 <input type="text" placeholder="Write priority here" id="priority" onChange={this.handelChange} value={this.state.priority}/>
                 <button type="submit"><span>Add Todo</span></button> 
                 </form>
             </div>
        )
    }
}

export default AddItem;