import React, { Component } from "react";
import logo from "./imgs/logo.gif";
import "./App.css";

import TodoItem from "./components/todoitem/todoitem";
import AddItem from "./components/additem/additem";

class App extends Component {
  state = {
    items: [
      { id: 1, text: "learn reactjs", priority: "important", style: "text " },
      {
        id: 2,
        text: "eat lunch",
        priority: "very important",
        style: "text style ",
      },
      { id: 3, text: "watch a movie", priority: "normal", style: "text " },
      {
        id: 4,
        text: "play online games",
        priority: "optional",
        style: "text ",
      },
    ],
  };

  // way number 1 of delete items
  deleteItemx = (id) => {
    let items = this.state.items;
    let i = items.findIndex((item) => item.id === id);
    items.splice(i, 1);
    this.setState({ items });
  };
  // way number 2 of delete items
  deleteItem = (id) => {
    let items = this.state.items.filter((item) => {
      return item.id !== id;
    });
    this.setState({ items });
  };
  addItem = (item) => {
    item.id = Math.random();
    let items = this.state.items;
    items.push(item);
    this.setState({ items });
  };
  fun = (item) => {
    let items = this.state.items;
    item.style === "text "
      ? (item.style = "text style")
      : (item.style = "text ");
    console.log(item.style);
    this.setState({ items });
  };
  render() {
    return (
      <div className="App">
        <div className="center">
          <img src={logo} className="App-logo" alt="logo" />
        </div>
        <TodoItem
          items={this.state.items}
          deleteItem={this.deleteItem}
          fun={this.fun}
        />
        <AddItem addItem={this.addItem} />
      </div>
    );
  }
}

export default App;
