import React from "react";
import "./todoitem.css";
import navIcon from "../../imgs/icon.png";
import removeIcon from "../../imgs/remove.png";
const TodoItem = (props) => {
  //hna m2olna4 this.props 34an de mo4 class component
  const { items, deleteItem, fun } = props;
  let length = items.length;
  const data =
    length > 0 ? (
      items.map((item) => {
        return (
          <div className="data" key={item.id}>
            <span className={item.style} onClick={() => fun(item)}>
              {item.text}
            </span>
            <img
              onClick={() => deleteItem(item.id)}
              src={removeIcon}
              alt="icon"
              height="35vmin"
            />
            <span className="priority">{item.priority}</span>
          </div>
        );
      })
    ) : (
      <div className="data">There's No Todo To Show</div>
    );

  return (
    <div className="container">
      <div className="header">
        <img src={navIcon} alt="icon" />

        <h1>Todo App</h1>
      </div>
      <div>{data}</div>
    </div>
  );
};
export default TodoItem;
